.container {
  padding: 20px;
  color: white;
  width: 100%;
  height: 100%;
}

.header {  
  width: 300px;
  height: 200px;
  padding: 10px;
  position: fixed;
  left: calc(50% - 150px);
  top: calc(50% - 100px);
}

.title, .modal-title {
  font-family: 'Pattaya', sans-serif!important;
  font-size: 26px;
  color: #006df0;
}

.title {
  margin: 0;
}
